import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { cn } from "@/lib/utils";
import { Zap, TrendingUp, TrendingDown } from "lucide-react";

interface ApproachingPair {
  pair: string;
  sector?: string;
  zScore: number;
  entryThreshold: number;
  halfLife?: number;
  signalStrength: number;
  direction?: string;
}

interface ApproachingListProps {
  pairs: ApproachingPair[];
}

export function ApproachingList({ pairs }: ApproachingListProps) {
  // Sort by signal strength descending
  const sortedPairs = [...pairs]
    .filter((p) => p.signalStrength >= 0.5) // Only show pairs at 50%+
    .sort((a, b) => b.signalStrength - a.signalStrength)
    .slice(0, 5);

  if (sortedPairs.length === 0) {
    return (
      <Card>
        <CardHeader>
          <CardTitle className="text-lg flex items-center gap-2">
            <Zap className="w-5 h-5" />
            Approaching Entry
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground text-sm text-center py-4">
            No pairs approaching entry threshold
          </p>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card>
      <CardHeader className="pb-3">
        <CardTitle className="text-lg flex items-center gap-2">
          <Zap className="w-5 h-5 text-primary" />
          Approaching Entry
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-2">
        {sortedPairs.map((pair) => {
          const pct = Math.min(100, Math.round(pair.signalStrength * 100));
          const isReady = pair.signalStrength >= 1;

          return (
            <div
              key={pair.pair}
              className={cn(
                "relative overflow-hidden rounded-lg border p-3",
                isReady
                  ? "border-emerald-500/50 bg-emerald-500/10"
                  : "border-border bg-card"
              )}
            >
              {/* Progress bar background */}
              <div
                className={cn(
                  "absolute inset-0 opacity-20",
                  isReady ? "bg-emerald-500" : "bg-primary"
                )}
                style={{ width: `${pct}%` }}
              />

              <div className="relative flex items-center justify-between">
                <div className="space-y-1">
                  <div className="flex items-center gap-2">
                    {pair.direction === "long" ? (
                      <TrendingUp className="w-4 h-4 text-emerald-400" />
                    ) : (
                      <TrendingDown className="w-4 h-4 text-red-400" />
                    )}
                    <span className="font-semibold">{pair.pair}</span>
                    {pair.sector && (
                      <Badge variant="outline" className="text-xs">
                        {pair.sector}
                      </Badge>
                    )}
                    {isReady && (
                      <Badge className="bg-emerald-500 text-xs">Ready</Badge>
                    )}
                  </div>
                  <div className="text-xs text-muted-foreground font-mono">
                    Z: {pair.zScore.toFixed(2)} → {pair.entryThreshold}
                    {pair.halfLife && (
                      <span className="ml-2">HL: {pair.halfLife.toFixed(1)}d</span>
                    )}
                  </div>
                </div>
                <div
                  className={cn(
                    "text-xl font-bold tabular-nums",
                    isReady ? "text-emerald-400" : pct >= 80 ? "text-yellow-400" : "text-muted-foreground"
                  )}
                >
                  {pct}%
                </div>
              </div>
            </div>
          );
        })}
      </CardContent>
    </Card>
  );
}


